<script>
import PostService from '../PostService'


export default {
  name: 'PostComponent',
  data() {
    return{
      posts: [],
      error: '',
      text: '',
      priority: '',
    }
  },
  async created(){
    try{
      this.posts = await PostService.getPosts();

    } catch(err){
      this.error = err.message;

    }
  },
  methods: {
    
     selectColor(event){
      let color = event.target.value.trim();
      this.color = color;
      let backgroundColour = '';
      
      if(color === 'High Priority'){
        backgroundColour ='red';
        
      }
      else if (color === 'Medium Priority'){
         backgroundColour ='yellow';
      }
      
     document.getElementById('cardHeader').style.background = backgroundColour;
     console.log(backgroundColour);
    },

    async createPost(){
      await PostService.insertPost(this.text, this.priority);
      this.posts = await PostService.getPosts();
      document.getElementById('tasksLabel').innerHTML = "TASKS";
    },
  
    async deletePost(id){
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    },
  }

};
</script>
